import { range } from "lodash"
import React, { Fragment, ReactNode, useEffect } from "react"
import {
  ArrowTable,
  ComponentProps,
  Streamlit,
  withStreamlitConnection,
} from "streamlit-component-lib"
import { CSVLink, CSVDownload } from "react-csv"

//the props of the Table class
interface TableProps {
  element: ArrowTable,
  evaluationColumn: number,
  smilesColumn: number,
  width: number,
  height: number
}

//the type of the state
type MyState = {
  checks: number[];
  result: (string|number)[][];
}

class Table extends React.PureComponent<TableProps> {
  

  state: MyState = {
    //an array with the length of the number of data Rows is created
    checks: range(0, this.props.element.dataRows).map((index) => {
      //with a map function the array is filled with the booleans of the checkbox column
      var content = this.props.element.getCell(index + 1, this.props.evaluationColumn + 1).content
      var formatedContent = 0
      if (content != null)
        formatedContent = Number(content)
      return formatedContent
    }),
    result: [[]]
  }
  public returnClicked = (): void => {
    //Streamlit.setComponentValue(this.state.checks)
    let result: (string|number)[][] = [['Row', 'Smiles', 'Rating']]
    for (let i = 0; i < this.state.checks.length; i++) {
      if (this.state.checks[i] != 0) {
        let element: (string|number)[]  = [i]
        const content = String(this.props.element.getCell(i + 1, this.props.smilesColumn+1).content)
        element.push(content)
        element.push(this.state.checks[i])
        result.push(element)
      }
      
    }
    this.setState({result: result})
  }


  public addClick = (row: number, checked: number): void => {
    //the checks array is updated with the value of the clicked checkbox
    this.state.checks[row - 1] = checked
    this.returnClicked()
  }



  public render = (): ReactNode => {
    const table = this.props.element //the arrow table
    const hasHeader = table.headerRows > 0 //if there are over 0 headerRows the table has a header
    const hasData = table.dataRows > 0 //if there are over 0 dataRows the table has data
    const caption = table.caption ? <caption>{table.caption}</caption> : null

    // the width and height that is passed through the props
    const width = this.props.width + 'px'
    const height = this.props.height + 'px'

    let size = {}
    if (this.props.width < 0) {
      size = { height: height, overflow: 'scroll' }
    }
    else {
      size = { height: height, width: width, overflow: 'scroll' }
    }

    return (

      <html>
        <body>
          <div style={size} >
            <table >
              {caption}
              {hasHeader && (
                <thead>
                  <TableRows isHeader={true} table={table} clickFunction={this.addClick} evaluationColumn={this.props.evaluationColumn + 1}
                /*the addClick function is passed down so that lower classes can call it and it is executed here*/ />
                </thead>
              )}
              <tbody>
                {hasData ? (
                  <TableRows isHeader={false} table={table} clickFunction={this.addClick} evaluationColumn={this.props.evaluationColumn + 1} />
                ) : (
                  <tr>
                    <td colSpan={table.columns || 1}>empty</td>
                  </tr>
                )}
              </tbody>
            </table >
          </div>
          <CSVLink  className='myButton' data={this.state.result} enclosingCharacter={``} filename={'smiles_rating.csv'}>Download Rating</CSVLink>
        </body>
      </html>
    )
  }

}

/**
 * Purely functional component returning a list of rows.
 *
 * isHeader     - Whether to display the header.
 * table        - The table to display.
 */

interface TableRowsProps {
  isHeader: boolean
  table: ArrowTable
  clickFunction: any
  evaluationColumn: number
}

const TableRows: React.SFC<TableRowsProps> = (props) => {
  const { isHeader, table } = props //the props deconstructed into isHeader and table
  const { headerRows, rows } = table //the table deconstructed into headerRows and normal rows

  //if the rows are header rows this elements uses the rows from 0 to the number of header rows
  //if the rows are not header rows the rows from the number of header rows to the number of rows are used
  const startRow = isHeader ? 0 : headerRows
  const endRow = isHeader ? headerRows : rows

  //an array is created from start to endRow and it is used to map over each row
  const tableRows = range(startRow, endRow).map((rowIndex) => (
    <tr key={rowIndex}>
      <TableRow rowIndex={rowIndex} table={table} clickFunction={props.clickFunction} evaluationColumn={props.evaluationColumn} />
    </tr>
  ))

  return <Fragment>{tableRows}</Fragment>
}

/**
 * Purely functional component returning a list entries for a row.
 *
 * rowIndex - The row index.
 * table    - The table to display.
 */

interface TableRowProps {
  rowIndex: number
  table: ArrowTable
  clickFunction: any
  evaluationColumn: number
}

const TableRow: React.SFC<TableRowProps> = (props) => {
  const { rowIndex, table } = props
  const { columns } = table

  //the function that was passed down from the table class is called with the row index and if the checkbox was checked
  const passRow = (e: any) => {
    props.clickFunction(rowIndex, Number(e.target.id))
  }

  // an array from 0 to the number of columns is created and used as a column index with map
  const cells = range(0, columns).map((columnIndex) => {
    //content is the data and type can either be blank, index, data or columns (titles)
    const { content, type } = table.getCell(rowIndex, columnIndex)

    //the content is formated so that it can be diplayed
    let formattedContent
    if (content != null) {
      formattedContent = content.toString()
    } else {
      formattedContent = ""
    }


    const id = rowIndex + '-' + columnIndex
    const checkboxID = 'checkbox_' + id

    switch (type) {
      case "blank": {
        return <th key={id} />
      }
      case "index": {
        return (
          <th key={id} scope="row" >
            {formattedContent}
          </th>
        )
      }
      case "columns": {
        return (
          <th key={id} scope="col" id={id}>
            {formattedContent}
          </th>
        )
      }
      case "data": {
        //if the content does not contain a html image
        if (formattedContent.indexOf('<img src=') === -1) {
          //either return a checked or unchecked checkbox depending on the data
          if (columnIndex === props.evaluationColumn) {
            return (
              <td id={id} key={id} >
                <Evaluation row={rowIndex} formattedContent={formattedContent} passRow={passRow} /></td>
            )
          }
          //if there is no checkbox just diplay the content
          return (
            <td key={id} id={id}>
              {formattedContent}
            </td>
          )
        } else {
          //display the content as HTML
          return (
            <td key={id} id={id}>
              <div dangerouslySetInnerHTML={{ __html: formattedContent }} />
            </td>
          )
        }
      }
      default: {
        throw new Error(`Cannot parse type "${type}".`)
      }
    }
  })

  return <Fragment>{cells}</Fragment>
}

interface EvaluationProps {
  row: number,
  formattedContent: string,
  passRow: any
}

const Evaluation: React.SFC<EvaluationProps> = (props) => {

  let style1 = { backgroundColor: 'red', color: 'black', border: '2px solid black' }
  let name1 = props.row + "_1"
  let style2 = { backgroundColor: 'yellow', color: 'black', border: '2px solid black' }
  let name2 = props.row + "_2"
  let style3 = { backgroundColor: 'green', color: 'black', border: '2px solid black' }
  let name3 = props.row + "_3"

  if (props.formattedContent == '1')
    style1 = { backgroundColor: 'black', color: 'white', border: '2px solid red' }
  else if (props.formattedContent == '2')
    style2 = { backgroundColor: 'black', color: 'white', border: '2px solid yellow' }
  else if (props.formattedContent == '3')
    style3 = { backgroundColor: 'black', color: 'white', border: '2px solid green' }

  const click = (e: any) => {
    props.passRow(e)
    let button1 = document.getElementsByName(name1)[0]
    let button2 = document.getElementsByName(name2)[0]
    let button3 = document.getElementsByName(name3)[0]
    if (button1 != null) {
      if (e.target.id == '1') {
        setOnClicked(button1)
        setOnNotClicked(button2)
        setOnNotClicked(button3)
      } else if (e.target.id == '2') {
        setOnClicked(button2)
        setOnNotClicked(button1)
        setOnNotClicked(button3)
      } else {
        setOnClicked(button3)
        setOnNotClicked(button2)
        setOnNotClicked(button1)
      }
    }

  }

  const setOnClicked = (button: any) => {
    button.style.border = "2px solid " + button.style.backgroundColor
    button.style.background = 'black'
    button.style.color = 'white'
  }
  const setOnNotClicked = (button: any) => {
    if (button.id == '1')
      button.style.background = 'red'
    else if (button.id == '2')
      button.style.background = 'yellow'
    else
      button.style.background = 'green'
    button.style.border = "2px solid black"
    button.style.color = 'black'
  }

  return <body>
    <button onClick={click} name={name1} id='1' style={style1}>1</button>
    <button onClick={click} name={name2} id='2' style={style2}>2</button>
    <button onClick={click} name={name3} id='3' style={style3}>3</button>
  </body>
}


/**
 * Dataframe example using Apache Arrow.
 */
const CustomDataframe: React.FC<ComponentProps> = (props) => {

  useEffect(() => {
    Streamlit.setFrameHeight()
  })

  return <Table element={props.args.data} evaluationColumn={props.args.evaluationColumn} smilesColumn={props.args.smilesColumn} width={props.args.width} height={props.args.height} />
}

export default withStreamlitConnection(CustomDataframe)
